.Header {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.Header-logo-circle {
  width: 10vw;
  height: 10vw;
  background-color: white;
  border-radius: 50%;
  margin-left: 10px;
  margin-right: 15px;
  display: flex;
  align-items: center;
}

.Header-logo {
  max-width: 8.1vw;
  max-height: 80%;
  margin-left: 2px;
}

.Header-title {
  font-size: calc(10px + 2.5vmin);
  color: #424b7d;
}
